@import url(https://fonts.googleapis.com/css?family=Montserrat|Roboto:900&display=swap);
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

html {
  font-size: 10px; }

:root {
  --main: rgb(124, 0, 0);
  --powder: #c4d4e0;
  --nantucket: #9aabb9;
  --apricot: #e2b49a;
  --jasmine: #e9c77b;
  --ink: hsl(204, 47%, 15%);
  --white: hsl(0, 0%, 95%); }

.App {
  height: 100vh;
  background-color: var(--ink); }

.Header {
  display: flex;
  justify-content: center;
  padding: 1.5rem; }
  .Header-title {
    font-size: 4rem;
    font-style: italic;
    font-family: 'Roboto', sans-serif;
    color: var(--white); }
  .Header-btn {
    background: transparent;
    outline: none;
    border: none;
    font-size: 2rem; }

.Slider {
  display: flex;
  overflow-x: scroll;
  overflow-y: visible; }

.Workout {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  background: var(--nantucket);
  padding: 2rem;
  margin-right: 3rem;
  border-radius: 10px;
  color: white; }
  .Workout:first-child {
    margin-left: 1rem; }
  .Workout-title {
    font-family: 'Roboto', sans-serif;
    font-size: 2.8rem;
    color: #f2f2f2; }
  .Workout-separator {
    align-self: flex-start;
    margin-top: 1rem;
    border: 0;
    height: 3px;
    width: 15%;
    background: white;
    border-radius: 5px; }
  .Workout-content {
    display: flex;
    justify-content: space-between;
    align-items: flex-end; }
  .Workout-exercises {
    flex: 1 1; }
  .Workout-exercise {
    list-style: none;
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.6rem; }
  .Workout-start {
    background: transparent;
    color: white;
    border: none;
    outline: none;
    font-family: 'Roboto', sans-serif;
    font-size: 3rem; }

.History {
  position: relative;
  height: 30%;
  margin: 3rem 1rem;
  display: flex; }
  .History-chart {
    overflow-x: scroll; }
  .History-point {
    position: absolute;
    width: 1rem;
    height: 1rem;
    background-color: var(--apricot);
    border-radius: 100px;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%); }
    .History-point-label {
      position: absolute;
      bottom: -2rem;
      left: -1.5rem;
      color: white;
      font-size: 1.6rem;
      font-family: 'Montserrat', sans-serif; }

